@import "./variables/variables";
.icon_list_container {
    display: flex;
    flex-direction: row;
    span {
        display: none;
    }
    li:not(:first-child) {
        margin-left: 18px;
    }
    li a svg {
        fill: rgba(0, 0, 0, 0.5);
        opacity: 0.3;
    }
    li a svg:hover {
        fill: #000000;
        opacity: 1;
    }
}